import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  currencyCode = "";
  amount = "";
  proxyType = "";
  proxyValue = "";
  isAmountEditable = "";
  expiryDate = "";
  name = "";
  discription = "";

  generatedQrString = "";
  constructor(public navCtrl: NavController) {

  }
  generateQr() {
    let qrData = {
      proxyType: this.proxyType,
      proxyValue: this.proxyValue, //"+621234567890123",
      isAmountEditable: this.isAmountEditable,
      expiryDate: this.expiryDate // "20201231"
    }
    let _generatedQrString = this.generateQR("PAYNOW", qrData, this.currencyCode, this.amount, this.name, this.discription);
    this.generatedQrString = _generatedQrString;

  }

    //Mock to generate QR code.
    generateQR(type, qrData, currencyCode, amount, name, discription) {
      if (type === "PAYNOW") {
        let paynowFormat = {
          "00": "SG.PAYNOW",
          "01": qrData.proxyType,
          "02": qrData.proxyValue,
          "03": qrData.isAmountEditable,
          "05": qrData.expiryDate
        };
        let additionalInfo = {}
        if (discription !== "") {
          additionalInfo = {
            "01": discription
          }
        }

        let sgqrFormat = {
          "00": "01", //PayloadFormat Indicator
          "01": "11",
          "26": paynowFormat,
          "53": currencyCode,
          "54": amount,
          "59": name,
          "62": additionalInfo
        }

        return this._getQrString(sgqrFormat);
      }
    }


    _getQrString(obj) {

      let _createdQr = "";
      console.log("This is obj ", obj)
      let keys: any = Object.keys(obj).map(d => parseInt(d)).sort();
      for (let k of keys) {
        let key = ((100 + parseInt(k)) + "").slice(1);
        if (typeof obj[key] === 'string') {
          _createdQr = _createdQr + key + ((100 + parseInt(obj[key].length)) + "").slice(1) + obj[key];
        } else if (typeof obj[key] === 'object') {
          let subString: any = this._getQrString(obj[key]);
          _createdQr = _createdQr + key + ((100 + parseInt(subString.length)) + "").slice(1) + subString;
        }
      }
      return _createdQr;

    }

}



